.at-drawer {
  position: relative;
  z-index: 900; }
  .at-drawer__mask {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    -webkit-transition: opacity 300ms cubic-bezier(0.36, 0.66, 0.04, 1);
    transition: opacity 300ms cubic-bezier(0.36, 0.66, 0.04, 1); }
  .at-drawer__content {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 9.81333rem;
    text-align: left;
    background-color: #FFF;
    opacity: 0;
    z-index: 1; }
  .at-drawer.at-drawer--left .at-drawer__content {
    left: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%); }
  .at-drawer.at-drawer--right .at-drawer__content {
    right: 0;
    -webkit-transform: translateX(100%);
            transform: translateX(100%); }
  .at-drawer.at-drawer--show .at-drawer__content {
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%); }

@-webkit-keyframes anim-show {
  100% {
    opacity: 1; } }

@keyframes anim-show {
  100% {
    opacity: 1; } }

@-webkit-keyframes anim-hide {
  100% {
    opacity: 0; } }

@keyframes anim-hide {
  100% {
    opacity: 0; } }
